@font-face {
    font-family: 'Barlow Condensed medium';
    src: url('../src/fonts/BarlowCondensed-Bold.ttf') format('truetype');
  }
  @font-face {
    font-family: 'Barlow Condensed';
    src: url('../src/fonts/BarlowCondensed-Thin.ttf') format('truetype');
  }
  @font-face {
    font-family: 'Bellefair';
    src: url('../src/fonts/Bellefair-Regular.ttf') format('truetype');
}

.technology {
    display: flex;
    flex-direction: column;
    position: fixed;


    background-image: url("/images/Technology.svg");
    height: 100vh;
    width: 100vw;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

  .num-btn {
    font-family: 'Bellefair';
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 37px;
color: white;
    text-align: center;
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;

    width: 80px;
    height: 80px;
    border-radius: 50%;
    cursor: pointer;

    margin-left: 500px;
    margin-bottom: 3.2vw;

    position: relative;
    background-color: transparent;
    
    mix-blend-mode: normal;
    opacity: 0.25;
    border: 1px solid #FFFFFF;

  
    outline: none;
  }
  .button{
    display: flex;
    margin-bottom: 30vw;
    // margin-left: 20vw;
}

.num-btn.active {
    opacity: 1;
    background-color: white;
    color: black;

  }
  
  .num-btn:focus {
    outline: none;

  }
  
  .num-btn:hover {
    opacity: 0.5;
  }